<template>
    <div>
        <ul>
            <li><router-link to="/">首页</router-link></li>
            <li @click="pushPage('/news',{id:1})">新闻</li>
            <li @click="pushPage('/goods',{id:2})">产品</li>
        </ul>
    </div>
</template>

<script>
    export default {
        methods:{
            pushPage(url,query={}){
                //用params传参:隐试传参
                //this.$router.push({name:"goods",params:{id:1}})
                //用query传参：参数显示地址栏
                this.$router.push({path:url,query:query});
            }
        }
    }
</script>

